.loader {
	width: 48px;
	height: 48px;
	border: 5px solid var(--ui);
	border-bottom-color: var(--white);
	border-radius: 50%;
	display: inline-block;
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
	margin: 0 auto;
}

@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
